.item {
    position: relative;
    width: 13em;
    height: 17em;
    background-color: green;
    margin-right: 6em;

    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 85% 15%;

    overflow: hidden;
}

.liveLink:hover, .githubLink:hover {
    cursor: pointer;
    scale: 1.05;
}

.blogItem:hover {
    cursor: pointer;
    animation: expandBlog 1s forwards;
}

img {
    width: 100%;
    grid-column: 1 / span 2;
    grid-row: 1;
    background-color: black;
}

.liveLink {
    grid-column: 1;
    grid-row: 2;
    background-color: white;
}

.githubLink {
    grid-column: 2;
    grid-row: 2;
    background-color: grey;
}

.readBlog {
    grid-column: 1 / span 2;
    grid-row: 2;
    background-color: grey;
}

@keyframes expandBlog{
    0% {
        grid-template-rows: 85% 15%;
    }

    100% {
        grid-template-rows: 50% 50%;
    }
}