.blog {
    top: 0;
    position: absolute;
    height: 100%;
    vertical-align: middle;
    background-color: #17181C;
    margin: auto;
    overflow: hidden;
}

.blogOpen {
    height: 100%;
    top: 0;
    position: absolute;
    background-color: orange;
    z-index:10;
    overflow: hidden;
}

.blogClosed {
    height: 100%;
    top: 0;
    position: absolute;
    background-color: orange;
    z-index:10;
    overflow: hidden;
}

.backButton {
    position: absolute;
    top: 1em;
    left: 1em;
    width:6em;
    height:2.5em;
    background-color: green;
}

.backButton:hover {
    cursor: pointer;
    scale: 1.05;
}

.header{
    position: relative;
    margin: auto;
    top: 6%;
    left: 2%;
    width: 75%;
    height: 45%;
    background-color: grey;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 15% 75% 10%;
}

.header > h1 {
    grid-row: 1;
}

.header > img {
    grid-row: 2;
    background-color: black;
    width: 100%;
    height: 100%;
}

.header > p {
    grid-row: 3;
}

.textArea{
    display: flex;
    position: relative;
    left: 2%;
    margin: auto;
    margin-top: 5%;
    width: 75%;
    height: 25%;
    background-color: grey;   
}

/*Animation Style Rules*/
.open{
    animation: openBlog 1s forwards;
}

.closed {
    animation: closeBlog 1s forwards;
}

@keyframes openBlog {
    0% {
        width: 0%;
    }
    100% {
        width: 100%;
    }
}

@keyframes closeBlog {
    0% {
        width: 100%;
    }
    100% {
        width: 0%;
    }
}