/*
    Spawn Box Rules
*/
.spawnBox {
    position: relative;
    width: 90%;
    height: 94%;
    background-color: #17181C;
    margin: auto;
    left: 5vw;
    top: 3vh;
    overflow: scroll;
    overflow-x: hidden;
}

.sections {
    display: flex;
    flex-direction: column;
    margin-top: 4em;
    padding: 2em;
}