@import url('https://fonts.googleapis.com/css?family=PT+Sans+Caption&display=swap');

.app {
    position: relative;
    background-color: #969695;
    height: 100vh;
}

.nav-bar{
    position: relative;
    display: flex;
    flex-direction: column;
    margin: auto;
    float: left;
    color: #F3F2F3;
    background-color: #17181C;
    width: 6vw;
    height: 100vh;
    z-index:10;
}

.navButton {
    width: 100%;
    height: 5%;
    background-color: #17181C;
    color: #F3F2F3;
    text-transform: uppercase;
}

.navButton:hover {
    cursor: pointer;
    color: #AB4257;
}


h3 {
    font-family: 'PT Sans Caption', sans-serif;
    padding-left: 1%;
    padding-top: 0.2%;
}
