@import url(https://fonts.googleapis.com/css?family=PT+Sans+Caption&display=swap);
@import url(https://fonts.googleapis.com/css?family=PT+Sans+Caption&display=swap);
.app {
    position: relative;
    background-color: #969695;
    height: 100vh;
}

.nav-bar{
    position: relative;
    display: flex;
    flex-direction: column;
    margin: auto;
    float: left;
    color: #F3F2F3;
    background-color: #17181C;
    width: 6vw;
    height: 100vh;
    z-index:10;
}

.navButton {
    width: 100%;
    height: 5%;
    background-color: #17181C;
    color: #F3F2F3;
    text-transform: uppercase;
}

.navButton:hover {
    cursor: pointer;
    color: #AB4257;
}


h3 {
    font-family: 'PT Sans Caption', sans-serif;
    padding-left: 1%;
    padding-top: 0.2%;
}

.blog {
    top: 0;
    position: absolute;
    height: 100%;
    vertical-align: middle;
    background-color: #17181C;
    margin: auto;
    overflow: hidden;
}

.blogOpen {
    height: 100%;
    top: 0;
    position: absolute;
    background-color: orange;
    z-index:10;
    overflow: hidden;
}

.blogClosed {
    height: 100%;
    top: 0;
    position: absolute;
    background-color: orange;
    z-index:10;
    overflow: hidden;
}

.backButton {
    position: absolute;
    top: 1em;
    left: 1em;
    width:6em;
    height:2.5em;
    background-color: green;
}

.backButton:hover {
    cursor: pointer;
    scale: 1.05;
}

.header{
    position: relative;
    margin: auto;
    top: 6%;
    left: 2%;
    width: 75%;
    height: 45%;
    background-color: grey;
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 15% 75% 10%;
}

.header > h1 {
    grid-row: 1;
}

.header > img {
    grid-row: 2;
    background-color: black;
    width: 100%;
    height: 100%;
}

.header > p {
    grid-row: 3;
}

.textArea{
    display: flex;
    position: relative;
    left: 2%;
    margin: auto;
    margin-top: 5%;
    width: 75%;
    height: 25%;
    background-color: grey;   
}

/*Animation Style Rules*/
.open{
    -webkit-animation: openBlog 1s forwards;
            animation: openBlog 1s forwards;
}

.closed {
    -webkit-animation: closeBlog 1s forwards;
            animation: closeBlog 1s forwards;
}

@-webkit-keyframes openBlog {
    0% {
        width: 0%;
    }
    100% {
        width: 100%;
    }
}

@keyframes openBlog {
    0% {
        width: 0%;
    }
    100% {
        width: 100%;
    }
}

@-webkit-keyframes closeBlog {
    0% {
        width: 100%;
    }
    100% {
        width: 0%;
    }
}

@keyframes closeBlog {
    0% {
        width: 100%;
    }
    100% {
        width: 0%;
    }
}
/*
    Top Page Style Rules
*/

.topPage {
    position: relative;
    width: 90vw;
    height: 100vh;
    overflow: hidden;
}
/*
    Spawn Box Rules
*/
.spawnBox {
    position: relative;
    width: 90%;
    height: 94%;
    background-color: #17181C;
    margin: auto;
    left: 5vw;
    top: 3vh;
    overflow: scroll;
    overflow-x: hidden;
}

.sections {
    display: flex;
    flex-direction: column;
    margin-top: 4em;
    padding: 2em;
}
.item {
    position: relative;
    width: 13em;
    height: 17em;
    background-color: green;
    margin-right: 6em;

    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 85% 15%;

    overflow: hidden;
}

.liveLink:hover, .githubLink:hover {
    cursor: pointer;
    scale: 1.05;
}

.blogItem:hover {
    cursor: pointer;
    -webkit-animation: expandBlog 1s forwards;
            animation: expandBlog 1s forwards;
}

img {
    width: 100%;
    grid-column: 1 / span 2;
    grid-row: 1;
    background-color: black;
}

.liveLink {
    grid-column: 1;
    grid-row: 2;
    background-color: white;
}

.githubLink {
    grid-column: 2;
    grid-row: 2;
    background-color: grey;
}

.readBlog {
    grid-column: 1 / span 2;
    grid-row: 2;
    background-color: grey;
}

@-webkit-keyframes expandBlog{
    0% {
        grid-template-rows: 85% 15%;
    }

    100% {
        grid-template-rows: 50% 50%;
    }
}

@keyframes expandBlog{
    0% {
        grid-template-rows: 85% 15%;
    }

    100% {
        grid-template-rows: 50% 50%;
    }
}
.section {
    display: flex;
    color: #F3F2F3;
    background-color: red;
    margin-bottom: 2em;
    margin-top: 1em;
    padding: 2em;
    align-content: normal;
    flex-direction: row;
}

h2 {
    white-space: nowrap;
    vertical-align: middle;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

.headerBox{
    margin-left: -2em;
    margin-right: 2em;
    margin-top: -2em;
    margin-bottom: -2em;
    width: 5em;
    background-color: black;
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;

}
