.section {
    display: flex;
    color: #F3F2F3;
    background-color: red;
    margin-bottom: 2em;
    margin-top: 1em;
    padding: 2em;
    align-content: normal;
    flex-direction: row;
}

h2 {
    white-space: nowrap;
    vertical-align: middle;
    transform: rotate(-90deg);
}

.headerBox{
    margin-left: -2em;
    margin-right: 2em;
    margin-top: -2em;
    margin-bottom: -2em;
    width: 5em;
    background-color: black;
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;

}